* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

article, section {
  display: block;
}

span {
  font: inherit;
  color: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

.people {
  grid-gap: 40px 30px;
  grid-template-columns: 200px 200px;
  place-content: center;
  margin: auto;
  display: grid;
}

.person {
  box-sizing: border-box;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  transition: transform 1s;
  display: flex;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person__photo {
  margin-bottom: 8px;
  position: relative;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  display: flex;
  position: absolute;
  top: 94px;
  right: 10px;
}

.person__image {
  border-radius: 50%;
  width: 100%;
  display: flex;
}

.person__image:hover {
  border-radius: 60px;
}

.person__name {
  color: #2f4f4f;
  text-align: center;
  margin-bottom: 9px;
  font-size: 20px;
  font-weight: 700;
  display: block;
}
